.App {
  height: 100vh;
  width: 100vw;
  background-color: antiquewhite;
  position: relative;
  overflow: hidden;
}

.game {
  position: absolute;
  height: 100%;
  background-color: rgb(220,220,220);
  width: 100%;
  overflow: hidden;
  bottom: 0;
}

@media screen and (min-height: 500px) {
  .game {
    height: 500px;
    bottom: 50%;
    transform: translateY(50%);
  }
}


.game-touch {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.menu-shrink {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 30px;
  height: 30px;
  z-index: 1110;
}

.menu-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1100;
  width: 100%;
  height: 100%;
}
.menu-blur {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
}
.menu-normal {
  position: absolute;
  margin: 0;
  list-style: none;
  padding: 0;
  height: 80%;
  width: 200px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: azure;
  display: flex;
  flex-direction: column;
}

.menu-normal li {
  width: 100%;
  flex-grow: 1;
  position: relative;
}

.menu-normal li button{
  position: absolute;
  margin: 0;
  padding: 5px 10px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.individual {
  position: absolute;
}

.individual canvas{
  position: absolute;
}

.scene-background {
  position: absolute;
  bottom: 0;
}

